<template>
    <div class="block">
        <h5 class="square-title">實際請款（請於期中報告通過後進行請款，實支實付上限50%）</h5>
        <p class="text-teal fw-bold py-3">核定經費: {{ project.ApprovedAmount?.toLocaleString() }}元</p>
        <div class="table-responsive">
            <table class="table align-middle gray-table">
                <thead>
                    <tr>
                        <th class="text-end">本期請款金額</th>
                        <th class="text-end">前期已撥付金額</th>
                        <th>累積實支金額</th>
                        <th class="text-end">累積經費執行率</th>
                        <th class="text-end">支用比</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td class="text-end">{{ amount }}</td>
                        <td class="text-end">--</td>
                        <td><input-integer v-model="amount"></input-integer></td>
                        <td class="text-end">{{ rate }}%</td>
                        <td class="text-end">--</td>
                    </tr>
                </tbody>
            </table>
        </div>
        <h5 class="square-title mt-4">檔案上傳</h5>
        <p class="text-pink mt-3 lh-base">請下載附件範本，填寫資料及公文用印後上傳（僅支援PDF）。</p>
        <div class="mt-4">
            <table class="table align-middle gray-table">
                <thead class="text-center">
                    <tr>
                        <th width="60">附件編號</th>
                        <th>附件名稱</th>
                        <th width="180" v-if="editable">狀態</th>
                        <th width="350">上傳附件</th>
                    </tr>
                </thead>
                <tbody>
                    <tr :key="item" v-for="(item, idx) in docs">
                        <td class="text-center">
                            {{ idx + 1 }}
                        </td>
                        <td>
                            <div>
                                <required-label>{{ item.Title }}</required-label>
                            </div>
                            <a class="btn btn-sm btn-teal-dark rounded-pill mt-2" :href="item.Template" style="width:140px" type="button" v-if="editable && item.Template">
                                <i class="fas fa-file-download me-1"></i>範本下載
                            </a>
                        </td>
                        <td class="text-center" v-if="editable">
                            <span v-if="item.Uploaded">已上傳</span>
                            <span class="text-pink" v-else>尚未上傳</span>
                        </td>
                        <td>
                            <input-file accept=".pdf" @file="(file) => add(item, file)" v-if="editable && (item.Multiple || !item.Uploaded)"></input-file>
                            <div class="tag-group mt-2 gap-1" v-if="item.Uploaded">
                                <template :key="file" v-for="(file) in item.Files">
                                    <span class="tag tag-green-light" v-if="!file.Deleted">
                                        <a class="tag-link my-1" :download="file.Name" :href="download(file.Path)">{{ file.Name }}</a>
                                        <button class="tag-btn" @click="file.Deleted = true" type="button" v-if="editable">
                                            <i class="fa-solid fa-circle-xmark"></i>
                                        </button>
                                    </span>
                                </template>
                            </div>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
    <div class="block-bottom bg-light-teal">
        <button class="btn btn-outline-teal me-3" @click="save()" type="button">暫存</button>
        <button class="btn btn-teal" @click="save(true)" type="button"><i class="fas fa-check"></i>提送</button>
    </div>
</template>

<script setup>
    const props = defineProps({
        id: { type: [Number, String] }
    });

    const amount = ref(0);
    const editable = true;
    const project = ref({});

    const docs = ref([
        { Type: 1, Title: "經費收支單", Template: "../../Template/CUL/經費收支單.docx", Files: [] },
        { Type: 2, Title: "發票憑證", Files: [], Excludes: ["1","2"] }
    ]);

    const rate = computed(() => {
        if (project.value.ApprovedAmount) {
            return Math.min(Math.round(amount.value * 100 / project.value.ApprovedAmount * 100) / 100, 100);
        }

        return 0;
    });

    const add = (item, file) => {
        if (file.Type === "application/pdf") {
            item.Files.push({
                Type: item.Type,
                Path: file.Path,
                Name: project.value.ProjectID + "_第一期請款_" + (item.Multiple ? file.Name : `${item.Title}.pdf`)
            });
        }
    };

    const download = api.download;

    const emit = defineEmits(["next"]);

    const load = () => {
    };

    const save = (submit) => {
    };

    onMounted(() => {
        api.culture("findApplication", { ID: props.id }).subscribe((result) => {
            project.value = result.Project;

            useProgressStore().init("culture", project.value);

            docs.value = docs.value.filter((doc) => !doc.Excludes?.includes(project.value.OrgCategory));

            docs.value.forEach((doc) => {
                doc.Uploaded = computed(() => doc.Files.filter((file) => !file.Deleted).length);
            });

            load();
        });
    });

    provide("editable", editable);
</script>
